
"use client";

import Link from 'next/link';
import type { Lecture } from '@/lib/types';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { ArrowRight, CheckCircle2, Clock, ListChecks } from 'lucide-react';
import { Progress } from '@/components/ui/progress';
import { useUserProgress } from '@/contexts/UserProgressContext';
import { getIconComponent } from '@/lib/icons'; // New import

interface LectureCardProps {
  lecture: Lecture;
}

export function LectureCard({ lecture }: LectureCardProps) {
  const { isLessonCompleted, isQuizCompleted } = useUserProgress();
  
  // Ensure lecture.items is treated as an array even if undefined/null from Firestore
  const items = Array.isArray(lecture.items) ? lecture.items : [];
  const totalItems = items.length;

  const completedItems = items.reduce((count, item) => {
    if (item.type === 'lesson' && isLessonCompleted(item.id)) {
      return count + 1;
    }
    if (item.type === 'quiz' && isQuizCompleted(item.id)) {
      return count + 1;
    }
    return count;
  }, 0);

  const progressPercentage = totalItems > 0 ? (completedItems / totalItems) * 100 : 0;
  const IconComponent = getIconComponent(lecture.iconName); // Use getIconComponent

  return (
    <Card className="flex flex-col h-full shadow-lg hover:shadow-xl transition-shadow duration-300 ease-in-out">
      <CardHeader>
        <div className="flex items-center gap-4 mb-2">
          <IconComponent className="h-10 w-10 text-primary shrink-0" />
          <CardTitle className="text-2xl font-semibold">{lecture.title}</CardTitle>
        </div>
        <CardDescription className="text-sm text-muted-foreground h-16 overflow-hidden">
          {lecture.description}
        </CardDescription>
      </CardHeader>
      <CardContent className="flex-grow">
        <div className="space-y-3">
          <div className="flex items-center text-sm text-muted-foreground">
            <ListChecks className="mr-2 h-4 w-4" />
            <span>{lecture.itemCount ?? totalItems} items</span>
          </div>
          <div className="flex items-center text-sm text-muted-foreground">
            <Clock className="mr-2 h-4 w-4" />
            <span>{lecture.estimatedTime}</span>
          </div>
           {totalItems > 0 && (
            <div>
              <div className="flex justify-between items-center mb-1">
                <span className="text-xs text-muted-foreground">Progress</span>
                <span className="text-xs font-medium text-primary">
                  {Math.round(progressPercentage)}%
                </span>
              </div>
              <Progress value={progressPercentage} aria-label={`${lecture.title} progress`} className="h-2" />
            </div>
          )}
        </div>
      </CardContent>
      <CardFooter>
        <Link href={`/lectures/${lecture.id}`} className="w-full">
          <Button className="w-full group">
            {progressPercentage === 100 ? (
              <>
                Review Lecture <CheckCircle2 className="ml-2 h-4 w-4" />
              </>
            ) : (
              <>
                Start Lecture <ArrowRight className="ml-2 h-4 w-4 transition-transform group-hover:translate-x-1" />
              </>
            )}
          </Button>
        </Link>
      </CardFooter>
    </Card>
  );
}
